const HomeVision = () => {
    return (
        <section id="section-vision">
            {/* <!-- title --> */}
            <h2>The FIRE Network Vision</h2>
            <span className="subtitle" data-nsfw-filter-status="swf">
                An innovative step forward for passive income generation
            </span>
            {/* <!-- content --> */}
            <div id="vision-content" className="container mx-auto">
                <div id="left-side">
                    <div>
                        <div className="title text-blue">
                            FIRE AIMS TO COMBINE REWARDS ACROSS 30+ YIELD PROTOCOLS
                        </div>
                        <p data-nsfw-filter-status="swf">
                            The idea of the FIRE project is to invest into many DeFi
                            high-yield protocols, ensuring a managed portfolio across projects
                            of varying risk.
                        </p>
                    </div>
                    <div>
                        <div className="title text-purple">
                            MANAGED RISK WITH A BALANCED SET OF INVESTMENTS
                        </div>
                        <p data-nsfw-filter-status="swf">
                            FIRE will research and independently verify each protocol that
                            will be invested in through an experienced team that eat, sleep,
                            and dream about DeFi.
                        </p>
                    </div>
                    <div>
                        <div className="title text-pink">
                            REDUCE FEES AND THE TIME SPENT CLICKING
                        </div>
                        <p data-nsfw-filter-status="swf">
                            The FIRE Network will move as one, and therefore its participants
                            will be exposed to 30+ DeFi protocols without the network fees,
                            the manual swapping, bridging, staking, bonding, etc.
                        </p>
                    </div>
                </div>
                <div id="img-diagram">
                    <img alt="" width="398" height="289" src="../img/diagram.png" data-xblocker="passed" style={{visibility: "visible"}} data-nsfw-filter-status="sfw"/>
                    <div id="img-diagram-shadow"></div>
                </div>
            </div>
            {/* <!-- chips --> */}
            <div id="chips-header">Benefits for FIRE Network Members</div>
            <div id="chips">
                <div className="f-row chip gap-1 f2-center">
                    <i className="fas fa-check-circle text-third"></i>Diversification
                </div>
                <div className="f-row chip gap-1 f2-center">
                    <i className="fas fa-check-circle text-third"></i>Lowered Risk
                </div>
                <div className="f-row chip gap-1 f2-center">
                    <i className="fas fa-check-circle text-third"></i>Less Fees
                </div>
                <div className="f-row chip gap-1 f2-center">
                    <i className="fas fa-check-circle text-third"></i>Less Hassle
                </div>
                <div className="f-row chip gap-1 f2-center">
                    <i className="fas fa-check-circle text-third"></i>Less Research
                </div>
            </div>
        </section>
    );
}

export default HomeVision;