const HomeHero = () => {
    return (
        <section id="section-hero" className="f-column f2-center">
        <h1 className="text-center">
          Access the world of DeFi through a multi-chain <br/>
          <span className="text-gradient" data-nsfw-filter-status="swf"> Yield Processing Node </span>
        </h1>
    
        <div id="hero-image">
          <img  alt="" id="hero-image-ring" width="106" src="../img/ring-color.svg" data-nsfw-filter-status="sfw" style={{visibility: "visible"}} data-xblocker="passed" />
          <img alt="" width="356" height="356" src="../img/square-cpu-nologo.png" data-xblocker="passed" style={{visibility: "visible"}} data-nsfw-filter-status="sfw" />
        </div>
    
        <div id="hero-text" className="text-2xl text-center">
          Earn Lifetime Rewards of
          <span className="text-green text-xx-large" data-nsfw-filter-status="swf"> 2600% APY </span><span className="second-title text-large" data-nsfw-filter-status="swf">
            <div className="tooltip">
              <i className="fa fa-info-circle"></i>
              <span className="tooltiptext" data-nsfw-filter-status="swf">(est.)</span>
            </div>
          </span>
        </div>
    
        <div id="hero-subtext" className="second-title text-center">
          Only 10 FIRE needed to get started -
          <a id="try-link" href="https://app.ring.financial/" style={{textDecoration: "underline"}} data-nsfw-filter-status="swf">
            try it now!
          </a>
        </div>
        {/* <!-- hero-cards --> */}
        <div id="hero-cards" className="container mx-auto">
          <div className="f-column card">
            <span className="text-blue" data-nsfw-filter-status="swf">yield-generating protocol</span>
            <p data-nsfw-filter-status="swf">
              FIRE aims to combine the returns from 30+ DeFi protocols carefully
              selected by our team and redistribute the yield to you. Hence, your
              risk is minimized and the fees are erased.
            </p>
          </div>
          <div className="f-column card">
            <span className="text-purple" data-nsfw-filter-status="swf">HASSLE-FREE &amp; EASY TO USE</span>
            <p data-nsfw-filter-status="swf">
              No maintenance fees to pay, subscriptions to manage, or software to
              run. The FIRE network introduces the innovative concept of virtual
              nodes that need less than 10 seconds to set up.
            </p>
          </div>
          <div className="f-column card">
            <span className="text-pink" data-nsfw-filter-status="swf">HIgh-YIELD LIFETIME REWARDS</span>
            <p data-nsfw-filter-status="swf">
              By being a part of the FIRE network, you are joining forces with
              thousands of node-holders, pooling your resources to generate
              high-yield rewards forever, paid out every 4 hours.
            </p>
          </div>
        </div>
      </section>
    
    );
}

export default HomeHero;