
import React from 'react';
import {connect} from 'react-redux';

class HomeHeader extends React.Component  {

    constructor(props) {
        super(props);
        this.state = {
            price_bnb: props.price_bnb,
            price_usd: props.price_usd
        }
    }

    render() {
        return (
            <nav className="nav-bar container mx-auto">
                {/* <!-- brand --> */}
                <div className="f-row f2-center gap-2" style={{ width: "167px" }}>
                    <img className="img-spin" width="48" height="48" src="../img/logo-color.png" alt="" />
                    <span id="brand-name" data-nsfw-filter-status="swf">FIRE</span>
                </div>
                {/* <!-- header stats --> */}
                <div id="header-stats" className="f-row gap-4">
                    <div className="f-column text-center">
                        <span id="bnb-price" className="text-yellow" data-nsfw-filter-status="swf">{this.state.price_bnb}</span>
                        <span data-nsfw-filter-status="swf"><span className="second-title" data-nsfw-filter-status="swf">FIRE</span>
                            <span style={{ margin: "0 4px", marginLeft: "2px" }} data-nsfw-filter-status="swf">/</span>BNB</span>
                    </div>
                    <div className="f-column text-center">
                        <span id="usd-price" className="text-green" data-nsfw-filter-status="swf">{this.state.price_usd}</span>
                        <span data-nsfw-filter-status="swf"><span className="second-title" data-nsfw-filter-status="swf">FIRE</span>
                            <span style={{ margin: "0 4px", marginLeft: "2px" }} data-nsfw-filter-status="swf">/</span>USD</span>
                    </div>
                    <div className="f-column text-center">
                        <span id="number-node" className="text-purple" data-nsfw-filter-status="swf">30000</span>
                        <span data-nsfw-filter-status="swf"><span className="second-title" data-nsfw-filter-status="swf">FIRE</span><span style={{ margin: "0 2px" }} data-nsfw-filter-status="swf">-</span>Nodes</span>
                    </div>
                </div>
                {/* <!-- launch button --> */}
                <a href="/app" className="launch-button" data-nsfw-filter-status="swf"> 
                {/* <Link to="/ring" className="launch-button" data-nsfw-filer-status> */}
                    LAUNCH APP
                {/* </Link> */}
                </a>
            </nav>
        );
    }
}

const mapStateToProps = state => {
    return {price_usd: state.price_usd, price_bnb: state.price_bnb};
}

export default connect(mapStateToProps)(HomeHeader);

